<template>
  <div id="remotevideo" class="videodisplay" >
    <span>{{videotip || status}}</span>
  </div>
</template>
<script>
export default {
  name: "RemoteStream",
  props:{
     videotip: {
      type: String,
      default: ''
    },
  },
  data(){
    return {
      status:"老师正在积攒能量"
    };
  },
  methods: {
    init(videoClient) {
      const that = this;
      that.splash.progress(2, that.$t("splash_video_sub"));
      videoClient.on("stream", function (id, type, token) {
        console.log("STREAM ADD :::", id, type, token);
        if (type == that.Global.UserType.TEACHER) {
          // that.status = "加载中...";
          videoClient.player(token, document.querySelector('#remotevideo'));
        };
      });

      videoClient.on("removed", function (id, type, token) {
        console.log("STREAM REMOVED :::", id, type, token);
        if (type == that.Global.UserType.TEACHER) {
          // that.status = "老师正在积攒能量";
          videoClient.stopPlayer(token);
        };
      });

      console.log('remote video add listen', videoClient);
    },
    close(){
      
    }
  }
};
</script>
 
<style lang="less" scoped>
.videodisplay {
  width: 100%;
  height: 100%;
  position: relative;
  background: url("../assets/image/video_tip.png") no-repeat center;
  &.fill span {
    top: 60%;
  }
  span {
    position: absolute;
    width: 100%;
    text-align: center;
    display: block;
    top: 70%;
    color: #FFF;
  }
  video::backdrop{
    background: #009;
  }
}
</style>

