<template>
  <ul class="chat-list" ref="chatList">
    <li class="chat-item" v-for="item in chatList" :key="item.chatIndex">

      <div class="lottie" v-if="isEmoji(item.text)">
        <span class="name">{{item.name}}</span>
        <span class="info">给老师送出</span>
        <Lottie
          class="lott"
          :emoticonObj="emoticonObj"
          :code="item.text"
          :width="103"
          :height="103"
        ></Lottie>
      </div>

      <div class="text" v-else>{{item.text}}</div>
    </li>
  </ul>
</template>

<script>
import Api from "@/service";
import Lottie from "./Lottie.vue";
import { mapState, mapActions } from "vuex";

export default {
  name: 'VideoChat',
  components: {
    Lottie
  },
  data() {
    return {
      chatIndex: 0,
      noMessage: false,
      message: "",
      chatList: [],
      emoticonObj: null,
      emoticonObjMap: {},
      preChatList: [],
      isSendingEmoji: false,
      isQueue: false
    };
  },
  computed: {
    ...mapState("user", {
      user: state => state
    }),
  },
  watch: {
    chatList(list) {
      this.scrollToBottom();
    }
  },
  methods: {
    pushMessage(data) {
      data.chatIndex = this.chatIndex++;
      if (data.mine) {
        this.chatList.push(data);
        this.removeChat();
      } else {
        this.preChatList.push(data);
      }
      this.sendEmojiList();
    },
    listen() {
      this.$groupChat.on("chat", (user, text, mine) => {
        this.pushMessage({ ...user, text, mine });
      });
    },
    isEmoji(val) {
      if (!val) return false;
      return !!this.emoticonObjMap[val];
    },
    buildEmojoMap() {
      if (!this.emoticonObj) {
        this.emoticonObjMap = {}
        return
      }

      let { baseUrl, emoticon: list } = this.emoticonObj;
      for (let { code, name } of list) {
        this.emoticonObjMap[code] = 1
      }
    },
    async getEmoticon() {
      let { isSuccess, result } = await this.Api.getEmoticon();
      if (isSuccess) {
        this.emoticonObj = result.list[0];
        this.buildEmojoMap()
      }
    },
    sendEmojiList() {
      if (this.isQueue) return;
      this.isQueue = true;

      const timer = setInterval(() => {
        if (this.preChatList.length > 50) {
          this.preChatList.splice(0, Math.ceil(this.preChatList.length / 2))
        }

        if (this.preChatList.length > 0) {
          this.chatList.push(this.preChatList.shift());
          this.removeChat();
        }
        else {
          clearInterval(timer);
          this.isQueue = false;
        }
      }, 500)
    },
    removeChat() {
      if (this.chatList.length > 4) {
        this.$nextTick(() => {
          this.$delete(this.chatList, 0)
        })
      }
    },
    scrollToBottom() {
      if (this.startScrool) {
        return;
      }

      this.startScrool = true;
      this.$nextTick(() => {
        //必须使用nextTick（在下次 DOM 更新循环结束之后执行延迟回调）
        setTimeout(() => {
          const chatListDom = this.$refs.chatList;

          const different = chatListDom.scrollHeight - chatListDom.clientHeight;
          const setp = parseInt(different / 20);
          if (chatListDom) {
            const timer = setInterval(() => {
              let savedScrollTop = chatListDom.scrollTop;
              if (chatListDom.scrollHeight <= chatListDom.scrollTop + chatListDom.clientHeight) {
                clearInterval(timer);
                this.startScrool = false;
                return;
              }

              chatListDom.scrollTop += setp;
              if (chatListDom.scrollTop == savedScrollTop) {
                clearInterval(timer);
                this.startScrool = false;
                return;
              }
            }, 60)
          }
        })
      });
    }
  },
  created() {
    this.listen();
    this.getEmoticon();
  }
}
</script>

