import Vue from 'vue'
import animate from 'animate.css'

import App from './App.vue'
import store from './store'
import i18n from './i18n'
import Global from './plugins/Global'
import Api from './plugins/Api'
import Sensors from '@/plugins/Sensors.js'
import '@/plugins/APM.js'

import Classroom from './plugins/Classroom'

import '@/assets/style/base.less'

Vue.config.productionTip = false

Vue.use(Global);
Vue.use(Classroom);
Vue.use(Api);
Vue.use(Sensors,{enable:false});

//引入动画库
Vue.use(animate);

window.vm = new Vue({
  store,
  i18n,
  render: h => h(App),
}).$mount('#app')
