<template>
  <div class="video">
    <remote-stream
      ref="remoteVideo"
      :class="{fill: scale}"
      :videotip="videotip"
      :userdata="JSON.stringify({id:teacher.id, type:teacher.type, 'extend_id': teacher.uuid})"
    ></remote-stream>
    <UserProfile></UserProfile>
    <slot></slot>
  </div>
</template>

<script>
import autoplay_src from '@/assets/autoplay/autoplay.mp3';
import RemoteStream from "./RemoteStream.vue";
import UserProfile from "./UserProfile.vue";
import Api from "@/service";
export default {
  components: {
    RemoteStream,
    UserProfile
  },
  props: {
    teacher: {
      type: Object,
      default: () => { }
    },
    appToken: String,
    token: Number,
    userType: Number,
    scale: Boolean
  },
  data() {
    return {
      appid: null,
      classId: 0,
      device: null,
      videotip: ""
    };
  },
  computed: {
    publisher() {
      return this.userType == this.Global.UserType.TEACHER;
    },
    videoProfile() {
      // return (this.course && this.course.url) ? '480P_3' : '480P_6';
      return '480P_6';
    }
  },
  methods: {
    init(appid, classId) {
      this.appid = appid;
      this.classId = classId;
      if (!this.publisher) {
        this.initVideoClient(classId, this.appToken, this.token);
      }
    },
    videoClientError(err) {
      //TODO 处理通用error 信息
      if (err == 'SOCKET_DISCONNECTED') {
        console.error('视频服务已经断开');
      }
    },
    initVideoClient(classId, appToken, token) {
      const that = this;
      that.splash.progress(2, that.$t("splash_video_init"));
      let videoClient = this.videoClient;
      that.$refs["remoteVideo"].init(that.videoClient);
      videoClient.on("error", that.videoClientError);

      videoClient.on('rejected', function (token) {
        that.alert.show({
          icon: "exit",
          text: that.$t("alert_error_banned_text"),
          label: that.$t("alert_error_refresh_label")
        }, () => {
          window.location.reload();
        });
      });

      videoClient.init(that.appid).then(() => {
        that.splash.progress(2, that.$t("splash_video_connect"));
        videoClient.connect(classId, appToken, token).then((token, id, type) => {
          that.splash.progress(2, that.$t("splash_video_pub"));
          that.splash.complete(that.$t("splash_complated"));
        }).catch(function (err) {
          that.alert.show({
            icon: "refresh",
            text: JSON.stringify(err),
            label: that.$t("alert_error_refresh_label")
          }, () => {
            window.location.reload();
          });
        });
      }).catch(function (err) {
        that.alert.show({
          icon: "refresh",
          text: JSON.stringify(err),
          label: that.$t("alert_error_refresh_label")
        }, () => {
          window.location.reload();
        });
      });
    },
    close() {
      this.videotip = "课程已经结束";
      this.closeVideoClient();
    },
    closeVideoClient() {
      if (this.videoClient) {
        this.videoClient.exit();
        this.videoClient = null;
      }
    }
  }
};
</script>

<style lang="less" scoped>
.video {
  background: rgba(255, 255, 255, 0.35);
  overflow: hidden;
  width: 788px;
  .fill {
    height: 100%;
  }
}
</style>

