const Types = {
    ON_RESIZE: "onResize"
};

export default {
    namespaced: true,
    state: {
        width: 1920,
        height: 1080,
        padding: 0,
        ratio: 1,
    },
    mutations: {
        [Types.ON_RESIZE](state, ratio) {
            state.ratio = ratio;
        },
    },
    actions: {
        onResize({ state, commit }, element) {
            function resize() {
                const contentWidth = element.clientWidth - state.padding * 2; //新宽度
                const contentHeight = element.clientHeight - state.padding; //新高度

                let ratio = contentWidth / state.width;
                const height = state.height * ratio;

                if (height > contentHeight) {
                    ratio = contentHeight / state.height;
                }

                commit(Types.ON_RESIZE, Number(ratio.toFixed(4)));
            }

            resize();
            setTimeout(resize, 50);
        }
    },
    getters: {}
}
