import * as types from './mutation-types'

export default {
    namespaced: true,
    state: {
        ws: '',
        im: {},
        phone: "***-***-****",
        appid: '',
        leaveUrl: '',
        loginUrl: '',
        Layout: {
            ratio: 1
        },
    },
    mutations: {
        [types.SETCONFIG](state, setting) {
            state.ws = setting['socket'];
            state.im = setting['im'];
            state.phone = setting['phone'];
            state.appid = setting['agora_key'];
            state.leaveUrl = setting['leave_url'];
            state.loginUrl = setting['login_url'];
        },
        [types.SETRATIO](state, ratio) {
            state.Layout.ratio = ratio;
        },
    },
    actions: {
        [types.SETCONFIG]({ commit }, setting) {
            commit(types.SETCONFIG, setting)
        },
        setRatio({ commit }, ratio) {
            commit(types.SETRATIO, ratio)
        }
    },
    getters: {}
}