const Types = {
    ADD_USER: 'addUser',
    UPDATE_USER: 'updateUser',
    MERGE_USER: 'mergeUser',
    UPDATE_DRAWING: 'updateDrawing',
    UPDATE_STAGE: 'updateStage',
    UPDATE_ANIMATE: 'updateAnimate',
    UPDATE_MUTED: 'updateMuted',
}
import Record from "@/service/Record";

export default {
    namespaced: true,
    state: {
        id: 0,
        name: '',
        type: 1,
        photo: null,
        token: 0,
        diamond: 0,
        entryTime: 0,
        leaveTime: 0,
        muted: false,
        drawing: true,
        animate: false,
        stage: false,
        point: null,
        online: false,
    },
    mutations: {
        [Types.ADD_USER](state, user) {
            state.id = user["user_id"];
            state.uuid = user.external_id;
            state.name = user["nickname"];
            state.type = user["user_type"];
            state.photo = user["avatar"];
            state.token = user["user_type"] + "" + user["user_id"];
            state.diamond = Number(user["diamond"] || 0);
            state.entryTime = user["entry_time"];
            state.leaveTime = user["leave_time"];
            state.muted = false;
            state.drawing = true;
            state.animate = false;
            state.stage = false;
            state.point = null;
        },
        [Types.UPDATE_USER](state, tools) {
            for (var id in tools) {
                var list = tools[id];
                list.forEach(function (item) {
                    if (id == state.id) {
                        switch (item.type) {
                            case Record.MIC:
                                state.muted = item.state == 2; //true静音,false 解除静音
                                break;
                            case Record.DRAWING:
                                state.drawing = item.state == 1;
                                break;
                            case Record.ANIMATE:
                                state.animate = item.state == 1;
                                break;
                        }
                    }
                });
            }
        },
        [Types.MERGE_USER](state, user) {
            state = Object.assign(state, user);
        },
        [Types.UPDATE_MUTED](state, enabled) {
            state.muted = enabled;
        },
        [Types.UPDATE_DRAWING](state, enabled) {
            state.drawing = enabled;
        },
        [Types.UPDATE_ANIMATE](state, enabled) {
            state.animate = enabled;
        }
    },
    actions: {
        addStudent({ commit }, student) {
            commit(Types.ADD_USER, student);
        },
        updateStudent({ commit }, tools) {
            commit(Types.UPDATE_USER, tools);
        },
        mergeStudent({ commit }, user) {
            commit(Types.MERGE_USER, user);
        },
        mutedStudent({ commit }, enabled) {
            commit(Types.UPDATE_MUTED, enabled);
        },
        drawingStudent({ commit }, enabled) {
            commit(Types.UPDATE_DRAWING, enabled);
        },
        animateStudent({ commit }, enabled) {
            commit(Types.UPDATE_ANIMATE, enabled);
        }
    },
    getters: {}
}