<template>
  <div
    class="btn"
    :class="{interactive:disabled}"
    @click="btnClickHandler"
  >
    <icon-font
      class="icon-font"
      :icon="icon"
    ></icon-font>
  </div>
</template>

<script>
export default {
  name: 'BaseButton',
  props: {
    icon: {
      type: String,
      default: ""
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    btnClickHandler(e) {
      if (!this.disabled) {
        this.$emit('change', e);
      }
    }
  }
}
</script>

<style lang="less" scoped>
.btn {
  width: 26px;
  height: 26px;
  background: rgba(62, 113, 224);
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  opacity: 0.65;
  &:hover {
    opacity: 1;
  }
  
  &.interactive {
    pointer-events: none;
  }
}
</style>

