<template>
  <div class="care">
    <div class="photo">
      <img :src="teacherPhoto" width="100%">
    </div>
    <div class="detail">
      <span>{{teacher.name}}</span>
      <!-- <span class="uid">ID: {{teaID}}</span> -->
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import user_photo from "./../assets/image/user_photo.png";
export default {
  name: "UserProfile",
  data() {
    return {
      isFollowed: false,
      followCount: 0,
    };
  },
  computed: {
    ...mapState("teacher", {
      teacher: state => state
    }),
    teaID() {
      let tid = this.teacher.id.toString();
      return tid.padStart(6, 0);
    },
    teacherPhoto() {
      return this.teacher.photo ? this.teacher.photo + "?imageView2/0/w/100" : user_photo;
    }
  }
};
</script>

