import * as types from './mutation-types'

export default {
    namespaced: true,
    state: {
        uuid:'',
        photo: null,
        name: "UU-Teacher",
        id: "",
        type: 2,
        time: null,
        online: false,
    },
    mutations: {
        [types.SETTEACHER](state, teacher) {
            state.uuid = teacher.external_id;
            state.photo = teacher.avatar;
            state.name = teacher.nickname;
            state.id = teacher.user_id;
            state.type = teacher.user_type;
            state.token = parseInt([state.type, state.id].join(''));
        },
        [types.TIMER_CHANGE](state, time) {
            state.time = time;
        },
        [types.MERGE_USER](state, user){
            for(let key in user){
                state[key] = user[key];
            }
            state.token = parseInt([state.type, state.id].join(''));
        }
    },
    actions: {
        setTeacher({ commit }, teacher) {
            commit(types.SETTEACHER, teacher);
        },
        mergeTeacher({commit}, user){
            commit(types.MERGE_USER, user);
        }
    },
    getters: {
        teacherLoaded(state) {
            return !!state.user_id;
        },
        classTime(state) {
            return state.time;
        },
        userName(state) {
            return state.nickname;
        }
    }
}