<template>
  <div :style="style" ref="lavContainer"></div>
</template>

<script>
export default {
  props: {
    emoticonObj: {
      type: Object,
      required: true
    },
    code: {
      type: String,
      required: true
    },
    height: Number,
    width: Number
  },
  data() {
    return {
      anim: null,
      style: {
        width: this.width ? `${this.width}px` : "100%",
        height: this.height ? `${this.height}px` : "100%",
        overflow: "hidden",
        margin: "0 auto"
      }
    };
  },
  methods: {
    init() {
      if (!this.code) return;

      let { baseUrl, emoticon: list } = this.emoticonObj;
      for (let { code, name } of list) {
        if (code == this.code) {
          let container = this.$refs.lavContainer;
          this.anim = lottie.loadAnimation({
            container: container,
            renderer: "svg",
            loop: true,
            path: baseUrl + name + ".json"
          });
          return;
        }
      }
    }
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    if (this.anim) {
      this.anim.destroy()
      this.anim = null
    }
  }
};
</script>