<template>
  <div id="app">
    <Room ref="room" v-if="!room.isClassOver"/>
    <ClassOver v-if="room.isClassOver" :isTeacher="isTeacher" :isClassOver="room.isClassOver"></ClassOver>
    <splash class="splash"></splash>
    <alert class="alert"></alert>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

import Room from './components/Room.vue';
import Alert from "@/components/Alert.vue";
import Splash from "@/components/Splash.vue";
import autoplay from '@/assets/autoplay/autoplay.mp3';
import ClassOver from "./components/ClassOver.vue";

export default {
  name: 'App',
  components: {
    Room,
    ClassOver,
    Alert,
    Splash
  },
  computed: {
    ...mapState("config", {
      config: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("room", {
      room: state => state
    }),
    ...mapState("teacher", {
      teacher: state => state
    }),
    isTeacher() {
      return this.user.type == this.Global.UserType.TEACHER;
    },
  },
  methods: {
    ...mapActions("config", ["setConfig"]),
    ...mapActions("user", ["addUser", "saveDevice"]),
    ...mapActions("room", ["setRoom", "browser", "pushStudent", "endTheClass", "roomUpdateUser"]),
    ...mapActions("paint", ["startDraw"]),
    ...mapActions("teacher", ["setTeacher"]),
    ...mapActions("student", ["addStudent", "updateStudent"]),
    checkPageProfile() {
      const that = this;
      var audio = document.createElement('audio');
      audio.src = autoplay;
      var promise = audio.play();
      audio.volume = 0.01;
      if (promise) {
        promise.then(function () {
          that.getClassInfo();
        }).catch(err => {
          console.log("autoplay 策略检查:不可用", err.name, err.message);
          that.alert.show({
            title: '准备好了吗?就要开启学习之旅了哦!',
            label: "立即开启"
          }, cb => {
            that.getClassInfo();
          });
        });
      }
    },
    setClock(entry_time, now) {
      this.$clock.startTime = entry_time || Date.now();
      this.$clock.serverTime = now || Date.now();
    },
    async getClassInfo() {
      const { isSuccess, code, message, result, now } = await this.Api.getClassInfo();
      if (isSuccess) {
        const { room_id, setting, page, course_num, start_time, end_time, courseware,
          teacher_note,
          courseware_name,
          student_note,
          users
        } = result;
        this.setConfig(setting);
        this.setRoom({
          group_name: result.group_name,
          room_id,
          course_num,
          start_time,
          end_time,
          teacher_note,
          courseware_name,
          student_note
        });
        this.setClock(parseInt(start_time), parseInt(now));

        if (users.length) {
          for (let user of users) {
            if (user.user_type == this.Global.UserType.TEACHER) {
              user.now = now;
              this.setTeacher(user);
              this.$sensor.emit('registerPage', { anchorID: user.external_id })
            } else { //学生的信息
              this.addStudent(user);
            }
          }
        }

        this.course.userType = this.user.type;
        this.course.dataProvider(result["courseware"]);
        this.splash.progress(3, "init...");
        //初始化
        this.$socket.init(room_id, setting['websocket']);
        setting['socket'] = this.$socket.getSocketItem();
        this.course.load_bak_list = result.setting['courseware'].list || [];
        this.course.load_bak_timeout = result.setting['courseware'].timeout || 10;

        this.socketInited = true;
        this.$sensor.emit('registerPage', { roomID: room_id });
        this.$refs['room'].initRoom();
        this.$socket.connect(this.config.ws);
      } else {
        this.alert.show({
          icon: "exit",
          text: message,
          label: this.$t("button_leave_label")
        }, () => {
          window.location.href = `/message?code=${code}&message=${message}`;
        });
      }
    },
    async getUserInfo() {
      const that = this;
      const { isSuccess, code, message, result: user } = await this.Api.getUserInfo();
      if (isSuccess) {
        that.addUser(user);
        this.$i18n.locale = this.user.type == this.Global.UserType.TEACHER ? "en_US" : this.$i18n.locale;
        document.title = this.$t("document_title");
        this.browserCheck(function (Browser) {
          console.log("Browser : ", Browser);
          if (that.user.type == that.Global.UserType.ADMIN) {
            that.checkPageProfile();
          } else {
            if (that.user.type == that.Global.UserType.TEACHER) {
              that.startDraw();
              that.getClassInfo();
            } else {
              that.checkPageProfile();
            }
          }
        });
      } else if (code == 101001006) {
        this.alert.show({
          icon: "exit",
          text: this.$t("class_is_over"),
          label: this.$t("button_leave_label")
        }, () => {
          window.location.href = `/message?code=${code}&message=${message}`;
        });
      } else if ([10008].indexOf(code) != -1) {
        window.location.hash = "classover";
        that.alert.show({
          icon: "exit",
          text: this.$t('class_timeout_text'),
          title: this.$t("class_timeout_title"),
          label: this.$t("button_leave_label"),
        }, () => {
          that.endTheClass().then(url => {
            if (url) {
              window.location.href = url || `/message?code=${code}&message=${message}`;
            }
          });
        });
      } else {
        this.alert.show({
          icon: "exit",
          text: message,
          label: this.$t("button_leave_label")
        }, () => {
          window.location.href = `/message?code=${code}&message=${message}`;
        });
      }
    },
    browserCheck(cb) {
      const that = this;
      let Browser = { download: 'https://www.uuabc.com/download.html' };
      const ua = navigator.userAgent.toLowerCase();
      let re = /(msie|firefox|chrome|opera|version).*?([\d.]+)/;
      let m = ua.match(re);
      Browser.name = m[1].replace(/version/, "safari");
      Browser.version = m[2];
      Browser.mainVersion = Number(m[2].split('.')[0]);

      if (['chrome'].indexOf(Browser.name) != -1) {
        if (Browser.name == 'chrome' && Browser.mainVersion > 59) {
          Browser.supported = true;
          this.browser(Browser);
          //正常上课
          cb(Browser);
        } else {
          Browser.supported = false;
          this.browser(Browser);
          //不建议使用
          this.alert.show({
            icon: "exit",
            text: this.$t('browser_text', { name: Browser.name, version: Browser.version }),
            label: this.$t("browser_label"),
            cancellabel: this.$t("browser_download"),
          }, cb, () => {
            location.href = Browser.download;
            cb && cb(Browser);
          });
        }
      } else {
        Browser.supported = false;
        this.browser(Browser);
        //不能上课, 去下载或返回上一页
        this.alert.show({
          icon: "exit",
          text: this.$t('browser_text', { name: Browser.name, version: Browser.version }),
          label: this.$t("browser_download"),
          cancellabel: this.$t("button_leave_label"),
        }, () => {
          location.href = Browser.download;
          cb && cb(Browser);
        }, () => {
          that.endTheClass().then(url => {
            window.location.href = url || `/message?code=0&message="Browser does not support"`;
            cb && cb(Browser);
          });
        });
      }
    }
  },
  created() {
    this.getUserInfo();
  }
}
</script>

<style lang="less" scoped>
@import url("assets/style/variables");
#app {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: #6d93f2;
  display: flex;
  align-items: center;
  justify-content: center;
  .splash {
    z-index: @zindex-splash;
  }
  .alert {
    z-index: @zindex-alert;
  }
}
</style>